(function(){
    'use strict';
    require('angular-ui-router');
    require('services/api/api.services');
    var auditsDirective = require('directives/audits/audits.directive')

    var MODULE_NAME = 'app.views.shipment-details';

    angular.module(MODULE_NAME,[
        'ui.router',
        'app.services',
        auditsDirective
    ]).config(Config).controller('ShipmentDetailsCtrl',Controller);

    /* @ngInject */
    function Controller($rootScope,$scope,$log,shipment_data,AppServices) {

        $scope.shipment = shipment_data;
        $scope.audits = [];

        function Init() {

            $log.debug('$scope.shipment: ' + JSON.stringify($scope.shipment,null,2));
            $rootScope.showActivity = true;

            AppServices.api.shipments.get_audits({id:$scope.shipment.shipment_identifier},function(result){

                $scope.audits = result;
                $rootScope.showActivity = false;
            })

        };



        Init();

    };

    /* @ngInject */
    function Config($stateProvider) {
        $stateProvider
            .state('app.shipment-details', {
                url: '/shipments/:id',
                views: {
                    'container@': {
                        template: require('./shipment-details.html'),
                        controller: 'ShipmentDetailsCtrl',
                        controllerAs: 'vm'
                    }
                },
                resolve:{
                    shipment_data:function($log,$stateParams,AppServices){

                        //$log.debug('$stateParams: ' + JSON.stringify($stateParams,null,2));
                        return AppServices.api.shipments.get({id:$stateParams.id}).$promise;
                    }
                }
            });
    };

    module.exports = MODULE_NAME;

})();


